 .products {
     width: 100%;
     display: flex;
     flex-direction: column;
     gap: 24px;
     margin: 31px 0 32px 0;

     &__product {
         width: 100%;
         display: flex;
         justify-content: space-between;
         align-items: center;
     }

     &__prImageText {
         display: flex;
         gap: 16px;
         align-items: center;
     }

     &__prImage {
         width: 64px;
         height: 64px;
         border-radius: 8px;
         overflow: hidden;
         background-color: var(--grey);
         background-size: cover;
     }

     &__prTitle {
         font-size: 15px;
         line-height: 25px;
     }

     &__prPrice {
         font-size: 14px;
         font-weight: 700;
         line-height: 25px;
         opacity: 0.5;
     }
 }

